.search {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 20px;
  padding: 10px;
  min-height: 100px;
}
.search-input {
  width: 200px;
}
.search-item {
  display: flex;
  align-items: center;
  min-width: 280px;
  justify-content: flex-end;
}
.search-button-group {
  display: flex;
  justify-content: space-between;
}
